    background-color: black;
}

p {
    font-size:smaller;
}
.container {
    width:300px;
    background-color: rgb(62, 80, 139);
    border-radius: 8px;
    display: flex;
    align-items: center;
    border: 1px solid black;
    flex-direction: column;
}

.text {
    color: white;
    font-size: medium;
    margin: 0px;
}

.weather-days-container{
    width:100%;
    display: flex;
    justify-content: space-around;
    margin-top: 20px;
}

.day-button {
    width:20%;
    color: white;
    background-color: #062661;
    border: 1px solid black;
    border-radius: 7px;
}

.button-img {
    width:100%;
